html {
  overflow-wrap: inherit;
}

.unselectable {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.calendar-navigate {
  padding: 10px;
  opacity: 0.5;
}

.calendar-navigate:hover {
  cursor: pointer;
  opacity: 0.9;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.calendar-title {
  margin-top: 0.83em;
  margin-bottom: 0.83em;
}

.calendar-body {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(7, minmax(40px, 1fr));
  grid-template-rows: 50px;
  grid-auto-rows: minmax(120px, auto);
}

.calendar-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.day {
  text-align: right;
  padding: 14px 0px;
  font-size: 14px;
  border-bottom: 1px solid;
  border-right: 1px solid;
  border-color: lightgray;
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 601px) {
  .day {
    padding: 14px 20 px 20 px 0px;
  }
}

.innerDay {
  display: flex;
  flex-direction: column; 
  width: 100%;
}

.day:nth-of-type(7n) {
  border-right: none;
}

.day-name {
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  border-bottom: 1px solid;
  border-top: 1px solid;
  line-height: 50px;
  font-weight: 500;
}

.event {
  box-sizing: border-box;
  margin: 1px 0px;
  font-size: 15px;
  height: 26px;
}

.below {
  z-index: -1;
}

.details {
  display : flex;
  font-size: 14px;
  align-items: center;
  margin-top: 12px;
  margin-bottom: 12px;
}

.display-linebreak {
  white-space: pre-line;
}

.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  border-bottom: 1px solid #aaa;
  margin: 0 0 10px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
}

.react-tabs__tab--selected {
  background: #fff;
  border-color: #aaa;
  color: black;
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

